@-webkit-keyframes typewriter {
    0% { width:0 }
    25% { width: 70% }
    48% { width: 100% }
    50% { width: 100% }
    55% { width: 100% }
    75% { width: 70%; }
    85% { width: 30%; }
    100% { width: 0; }
}

@keyframes typewriter {
    0% { width:0 }
    25% { width: 70% }
    48% { width: 100% }
    50% { width: 100% }
    55% { width: 100% }
    75% { width: 70%; }
    85% { width: 30%; }
    100% { width: 0; }
}
  
@-webkit-keyframes blinking-cursor {
    from { border-color: transparent }
    to { border-color: #4285F4}
}
  
@keyframes blinking-cursor {
    from { border-color: transparent }
    to { border-color: #4285F4}
